	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Website Boy","date":"05 Nov 2002","slug":"/2002-11-05-website-boy/","tags":"Y2002"},"html":"<p>After <a href=\"http://www.papascott.de/index.php?p=1839&more=1&c=1\">gracing the pages</a> of the magazine of the national <em>Tagesmütter</em> association, Christopher is now doing online advertising for <a href=\"http://www.tagesmuetter-niedersachsen.de/bundesverband/aufgaben.html\">the state association for Niedersachsen</a>. Well, he's being used for space-filling decoration, anyway. Gratis.</p>\n<p><a href=\"http://www.tagesmuetter-niedersachsen.de/bundesverband/aufgaben.html\"><img alt=\"crhdeko.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/11/crhdeko.jpg\" width=\"235\" height=\"261\" border=\"0\" /></a></p>"},"next":{"fields":{"title":"Vote For Me","date":"05 Nov 2002","slug":"/2002-11-05-vote-for-me/"}},"previous":{"fields":{"title":"Deep Dark Links","date":"06 Nov 2002","slug":"/2002-11-06-deep-dark-links/"}}},{"node":{"fields":{"title":"Vote For Me","date":"05 Nov 2002","slug":"/2002-11-05-vote-for-me/","tags":"Y2002"},"html":"<p>Election Day in the US today, for those who've been in a cave the past few weeks. I didn't think to request an absentee (er, I mean <a href=\"http://recorder.maricopa.gov/absentee.htm\">'early voting'</a>) ballot until it was too late. Could one of my readers in the States please cast an extra ballot for me? I'll trust your judgement on who you vote for, just let me know who they were. Thanks!</p>"},"next":{"fields":{"title":"Where Was Everybody?","date":"04 Nov 2002","slug":"/2002-11-04-where-was-everybody/"}},"previous":{"fields":{"title":"Website Boy","date":"05 Nov 2002","slug":"/2002-11-05-website-boy/"}}},{"node":{"fields":{"title":"Where Was Everybody?","date":"04 Nov 2002","slug":"/2002-11-04-where-was-everybody/","tags":"Y2002"},"html":"<p>My blogroll was going crazy over the weekend. <a href=\"http://www.blogrolling.com\">Blogrolling</a> was getting stale data from from <a href=\"http://blo.gs\">blo.gs</a>, and kept backdating the some of the links. And I thought everyone was just taking the weekend off.</p>"},"next":{"fields":{"title":"Islamic party wins in Turkey","date":"03 Nov 2002","slug":"/2002-11-03-islamic-party-wins-in-turkey/"}},"previous":{"fields":{"title":"Vote For Me","date":"05 Nov 2002","slug":"/2002-11-05-vote-for-me/"}}}],"pathPrefix":"","first":false,"last":false,"index":821,"pageCount":1101}}

